.button {
    position: relative;
    overflow: hidden;
    height: 2.8rem;
    width: 2.8rem;
    padding-bottom: 0.5rem;
    /* padding: 0 1.2rem 0.2rem 1.2rem; */
   
    margin-right: 0.5rem;
    border-radius: 50%;
    background: #ffffff;
    background-size: 400%;
    color: #0e0e0e;
   border: 1px solid #000;
  }
  .button:hover::before {
    transform: scaleX(1);
  }
  .buttonContent {
    position: relative;
    z-index: 1;
    font-size: 1.2rem;
  }
  .button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: 0 50%;
    width: 100%;
    height: inherit;
    border-radius: inherit;
    background: linear-gradient(
      82.3deg,
      rgba(150, 93, 233, 1) 10.8%,
      rgba(99, 88, 238, 1) 94.3%
    );
    transition: all 0.475s;
  }